import {  
	mapState,  
	mapMutations  
} from 'vuex';  
export default {
	data() {
		return {
			ifLoadUserInfo:false,
			avatarUrl:String,
			city:String,
			gender:2,
			nickName:String
		}
	},
	methods: {  
		...mapMutations(['setLoginStatus','setUserInfo'])  
	},
	watch:{
		ifLoadUserInfo(newval,oldval){
			if(newval){
				wx.cloud.callFunction({
					name:'checkUserID',
					success:(res)=>{
						if(res.result.length===0){
							wx.cloud.callFunction({
								name:'addUser',
								data:{
									time: new Date(),
									nickName:this.nickName,
									gender:this.gender,
									city:this.city,
									avatarUrl:this.avatarUrl
								},
								success:(res)=>{
									console.log(res);
								}
							})
						}
					},
					fail:(res)=>{
						console.log(res)
					}
				})
			}
		}
	},
	onLoad() {
		uni.login({
		  provider: 'weixin',
		  success:(loginRes)=>{
		    // 获取用户信息
		    uni.getUserInfo({
		      provider: 'weixin',
		      success: (infoRes)=> {
				  console.log(infoRes.userInfo.avatarUrl)
				this.setLoginStatus();
				this.setUserInfo({
					avatarUrl:infoRes.userInfo.avatarUrl,
					city:infoRes.userInfo.city,
					gender:infoRes.userInfo.gender,
					nickName:infoRes.userInfo.nickName
				})
		        // console.log('用户昵称为：' + infoRes.userInfo.nickName);
				this.avatarUrl = infoRes.userInfo.avatarUrl;
				this.nickName = infoRes.userInfo.nickName;
				this.city = infoRes.userInfo.city;
				this.gender = infoRes.userInfo.gender;
				this.ifLoadUserInfo=true;
		      }
		    });
		  }
		});
	}
}
